/* 全站樣式 */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
}

header {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
}

nav ul li a:hover {
  color: #007bff;
}

h1 a {
  color: white; /* 設定連結顏色 */
  text-decoration: none; /* 移除底線 */
}

h1 a:visited {
  color: white; /* 確保已訪問過的連結仍保持黑色 */
}

h1 a:hover {
  color: #007bff; /* 滑鼠懸停時變藍 */
}

h1 a:active {
  color: red; /* 點擊時變紅 */
}

/* 搜尋框容器 */
.search-container {
  display: flex; /* 使搜尋框和按鈕水平排列 */
  align-items: center; /* 垂直對齊 */
  gap: 10px; /* 在搜尋框和按鈕之間留出間隙 */
  max-width: 500px; /* 設定搜尋區域的最大寬度 */
  margin: 0 auto; /* 讓搜尋區域在頁面中居中 */
}

/* 搜尋框樣式 */
.search-bar {
  width: 100%; /* 使搜尋框填滿容器 */
  padding: 10px 15px; /* 內邊距 */
  font-size: 1rem; /* 設置字體大小 */
  border: 1px solid #ccc; /* 邊框顏色 */
  border-radius: 4px; /* 圓角 */
  outline: none; /* 移除聚焦時的藍色邊框 */
}

/* 當搜尋框聚焦時的效果 */
.search-bar:focus {
  border-color: #007bff; /* 聚焦時邊框顏色變藍 */
}

/* 搜尋按鈕樣式 */
.search-btn {
  margin: 0;
  padding: 10px 10px; /* 內邊距 */
  background-color: #007bff; /* 藍色背景 */
  color: #fff; /* 白色字體 */
  border: none; /* 無邊框 */
  border-radius: 4px; /* 圓角 */
  cursor: pointer; /* 鼠標懸停時顯示為可點擊 */
  font-size: 1rem; /* 字體大小 */
}

/* 搜尋按鈕懸停效果 */
.search-btn:hover {
  background-color: #0056b3; /* 懸停時背景顏色變深 */
}

/* 商品分類 */
.categories {
  text-align: center;
  margin: 20px 0;
}

.categories button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
}

.categories button:hover {
  background-color: #0056b3;
}

/* 商品區塊 */
.products {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.product {
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 250px;
  text-align: center;
}

/* 🔹 小螢幕時的響應式調整 */
@media (max-width: 576px) {
  .products {
      flex-direction: column;
      align-items: center;
  }

  .product {
      width: 100%;
      max-width: 350px; /* 避免商品過寬 */
  }
}

.product img {
  width: 100%;
  height: 200px; /* 設置統一高度 */
  object-fit: contain; /* 裁剪圖片以填滿容器 */
  border-radius: 10px;
}


.product a {
  display: block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
}

button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 10px;
}

button:hover {
  background-color: #218838;
}

.cart {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  color: #333;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

th {
  background-color: #007bff;
  color: white;
}

img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 5px;
}

input[type="number"] {
  width: 50px;
  text-align: center;
}

.remove-btn {
  background-color: red;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
}

.remove-btn:hover {
  background-color: darkred;
}

.total {
  text-align: right;
  font-size: 1.2rem;
  margin-top: 10px;
}

/* for checkout button */
.checkout-btn {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  margin-left: auto;
  display: block; /* 或放在 flex 容器中 */
}

/* for total price */
.total-price {
  margin-right: 5px;
  font-size: 1.0rem;
  text-align: right;
}

.checkout-btn:hover {
  background-color: #218838;
}

/* 聯絡表單 */
.contact {
  text-align: center;
  margin-top: 40px;
}

.contact-form {
  max-width: 500px;
  margin: 0 auto;
  text-align: left;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

label {
  font-weight: bold;
  margin-bottom: 5px;
}

input, textarea {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
}

button {
  width: fit-content;
  padding: 10px 30px;
  background-color: #007bff;
  color: white;
  font-size: 1rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #0056b3;
}


/* 頁尾 */
footer {
  text-align: center;
  padding: 10px;
  background-color: #333;
  color: white;
  margin-top: 20px;
}
